import { createStore } from 'vuex'

export default createStore({
  state: {
	  cartList: {
		  
	  }
  },
  getters: {
  },
  mutations: {
	  addItemToCart(state,payload){
		  const { shopId,productId,productInfo,num } = payload;
		  let shopInfo = state.cartList[shopId]
		  if(!shopInfo) { shopInfo={} }
		  let product = shopInfo[productId]
		  if(!product) {
			  product = productInfo
			  product.count = 0
		  }
		  product.count += num
		  if(num>0) { product.check = true}
		          if(product.count<0){
		            product.count =0
		          }
		          shopInfo[productId] = product
		          state.cartList[shopId] = shopInfo
		  if(productId==1){
			  localStorage.productList1 = product.count;
		  }
		  if(productId==2){
		  	  localStorage.productList2 = product.count;
		  }
		  if(productId==3){
		  	  localStorage.productList3 = product.count;
		  }
		  if(productId==4){
		  	  localStorage.productList4 = product.count;
		  }
	  },
	  changeCartCheck(state,payload){
		  const { shopId,productId } = payload;
		  const product = state.cartList[shopId][productId]
		  product.check = !product.check
	  },
	  cleanCart(state,payload){
		  const { shopId } = payload
		  state.cartList[shopId] = {}
	  },
	  setCartCheck(state,payload){
		  const {shopId} = payload
		  const products = state.cartList[shopId]
		  if(products) {
			  for(let i in products){
				  const product = products[i]
				  product.check = true
			  }
		  }
	  }
  },
  actions: {
  },
  modules: {
  }
})
