<template>
	<div class="content">
		<div class="category">
			<div
			:class="{'category_item':true,'category_item--active':currentTab === item.tab}"
				v-for="item in categories"
				:key="item.name"
				@click="() => handleClick(item.tab)"
			>{{item.name}}</div>
		</div>
		<div class="product">
			<div 
				class="product_item"
				v-for="item in contentList"
				:key="item._id"
			>
				<img class="product_item_img" :src="item.imgUrl"/>
				<div class="product_item_detail">
					<h4 class="product_item_title">{{item.name}}</h4>
					<p class="product_item_sales">月售：{{item.sales}}件</p>
					<p class="product_item_price">
						<span class="product_item_yen">&yen;{{item.price}}</span>
						<span class="product_item_origin">&yen;{{item.oldPrice}}</span>
					</p>
				</div>
				<div class="product_number">
					<span class="product_number_minus" @click="() => {addItemToCart(shopId,item._id,itme,-1)}">-</span>
					{{cartList?.[shopId]?.[item._id]?.count || 0}}
					<span class="product_number_plus" @click="() => {addItemToCart(shopId,item._id,item,1)}">+</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { useRouter,useRoute } from 'vue-router'
	import {reactive,toRefs} from 'vue';
	import {get} from '../../utils/request.js';
	import { useStore } from 'vuex'
	export default{
		name:'Content',
		setup(){
			
			const categories = [
				{name:'全部商品',tab:'all'},
				{name:'秒杀',tab:'seckill'},
				{name:'新鲜水果',tab:'fruit'},
				
			]
			
			const data = reactive({
				currentTab:categories[0].tab,
				contentList:[]
			});
			const route = useRoute()
			const shopId = route.params.id
			const store = useStore()
			const { cartList } = toRefs(store.state)
			const addItemToCart = (shopId,productId,productInfo,num) =>{
				store.commit('addItemToCart',{shopId,productId,productInfo,num})
			}
			const getContent = async (tab) => {
				const result = await get('/api/shop/1/products',{tab})
				if(result?.errno === 0){
					data.contentList = result.data
				}
			}
			const handleClick = (tab) => {
				getContent(tab)
				data.currentTab = tab
			}
			getContent('all')
			
			const { contentList,currentTab} = toRefs(data)
			return {contentList,currentTab,categories,handleClick,cartList,shopId,addItemToCart}
		}
	}
</script>

<style lang="scss" scoped>
	.content{
		display: flex;
		position: absolute;
		left: 0;
		right: 0;
		top: 1.5rem;
		bottom: .5rem;
	}
	.category{
		overflow-y: scroll;
		height: 100%;
		width: .76rem;
		background: #F5F5F5;
		&_item{
			line-height: .4rem;
			text-align: center;
			font-size: 14px;
			color: #333;
			&--active{
				background: #FFF;
			}
		}
	}
	.product{
		overflow-y: scroll;
		flex: 1;
		&_item{
			position: relative;
			display: flex;
			padding: .12rem 0;
			margin: 0 .16rem;
			border-bottom: .01rem solid #F1F1F1;
			&_img{
				width: .68rem;
				height: .68rem;
				margin-right: .16rem;
			}
			&_title{
				margin: 0;
				line-height: .2rem;
				font-size: .14rem;
				color: #333;
			}
			&_sales{
				margin: .06rem 0;
				font-size: .12rem;
				color: #333;
			}
			&_price{
				margin: 0;
				line-height: .2rem;
				font-size: .14rem;
				color: #E93B3B;
			}
			&_yen{
				font-size: .12rem;
			}
			&_origin{
				margin-left: .06rem;
				line-height: .2rem;
				font-size: .12rem;
				color: #999;
				text-decoration: line-through;
			}
		}
		.product_number{
			position: absolute;
			right: 0;
			bottom: .12rem;
			&_minus,&_plus{
				display: inline-block;
				width: .2rem;
				height: .2rem;
				line-height: .16rem;
				border-radius: 50%;
				font-size: .2rem;
				text-align: center;
			}
			&_minus{
				border: .01rem solid #666;
				color: #666;
				margin-right: .05rem;
			}
			&_plus{
				background: #0091FF;
				color:#FFF;
				margin-left: .05rem;
			}
		}
	}
</style>
