<template>
	<div class="mask" v-if="showCart" @click="cartShow"></div>
	<div class="cart">
		<div class="product" v-if="showCart">
			<div class="product_header">
				<div class="product_header_all">
					<span
						class="product_header_icon iconfont"
						v-html="allChecked ?  '&#xe637;' : '&#xead9;'"
						@click="() => setCartCheck(shopId)">
						</span>
						全选
				</div>
				<div class="product_header_clear">
					<span 
						class="product_header_clear_btn"
						@click="() => cleanCart(shopId)"
					>清空购物车</span>
				</div>
				
			</div>
			<template v-for="item in productList"
				:key="item._id">
			<div class="product_item" v-if ="item.count > 0">
				<div class="product_item_checked iconfont"
					v-html="item.check ? '&#xe637;' : '&#xead9;'"
					@click="() => {changeCartCheck(shopId,item._id)}"
					>
				</div>
				<img class="product_item_img" :src="item.imgUrl"/>
				<div class="product_item_detail">
					<h4 class="product_item_title">{{item.name}}</h4>
					<p class="product_item_price">
						<span class="product_item_yen">&yen;{{item.price}}</span>
						<span class="product_item_origin">&yen;{{item.oldPrice}}</span>
					</p>
				</div>
				<div class="product_number">
					<span class="product_number_minus" @click="() => {addItemTocart(shopId,item._id,item,-1)}">-</span>
						{{item.count || 0}}
					<span class="product_number_plus" @click="() => {addItemTocart(shopId,item._id,item,1)}">+</span>
				</div>
			</div>
			</template>
		</div>
		<div class="check">
			<div class="check_icon">
				<img src="http://www.dell-lee.com/imgs/vue3/basket.png"
					class="check_icon_img"
					@click="cartShow"
				/>
				<div class="check_icon_tag">{{total}}</div>
			</div>
			<div class="check_info">
				总计：<span class="check_info_price">&yen;{{priceTotal}}</span>
			</div>
			<div class="check_btn">去结算</div>
		</div>
	</div>
</template>

<script>
	import {useRouter,useRoute} from 'vue-router';
	import {useStore} from 'vuex';
	import {toRefs} from 'vue';
	import {computed,ref} from 'vue';
	export default{
		name:'Cart',
		setup(){
			const route = useRoute();
			const shopId = route.params.id
			const store = useStore()
			const cartList = store.state.cartList
			
			const {cartList1} = toRefs(store.state)
			
			const total = computed(() =>{
				const productList = cartList[shopId]
				let count = 0
				if(productList){
					for (let i in productList){
						const product = productList[i]
						count += product.count
					}
				}
				return count
			})
			
			const priceTotal = computed(() => {
				const productList = cartList[shopId]
				let count = 0
				if(productList){
					for (let i in productList){
						const product = productList[i]
						if(product.check){
						count += (product.count * product.price)
						}
					}
				}
				return count.toFixed(2)
			})
			const productList = computed(() => {
				const productList = cartList[shopId] || []
				return productList
			})
			localStorage.productList =	productList;
			const addItemTocart = (shopId,productId,productInfo,num) => {
				store.commit('addItemTocart',{shopId,productId,productInfo,num})
			}
			const changeCartCheck = (shopId,productId) => {
				store.commit('changeCartCheck',{shopId,productId})
			}
			const cleanCart = (shopId) => {
				store.commit('cleanCart',{shopId})
			}
			const setCartCheck = (shopId) =>{
				store.commit('setCartCheck',{shopId})
			}
			const allChecked = computed(() =>{
				const productList = cartList[shopId]
				let result = true
				if(productList){
					for(let i in productList){
						const product = productList[i]
						if(product.count>0&&!product.check){
							result = false
						}
					}
				}
				return result
			})
			const showCart = ref(false)
			const cartShow = () => {
				showCart.value = !showCart.value
			}
			return {total,priceTotal,showCart,cartShow,shopId,productList,addItemTocart,changeCartCheck,cleanCart,setCartCheck,allChecked}
		}
	}
</script>

<style lang="scss" scoped>
	.mask{
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		background: rgba(0,0,0,.5);
		z-index: 1;
	}
.cart{
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 2;
	background: #FFF;
}
	.product {
		overflow-y: scroll;
		flex: 1;
		background: #FFFFFF;
		&_header {
			display: flex;
			line-height: .52rem;
			border-bottom: 1px solid #F1F1F1;
			font-size: .14rem;
			color: #333;
			&_checked {
				line-height: .5rem;
				margin-right: .2rem;
				color: #0091FF;
				font-size: .2rem;
			}
			&_all {
				width: .78rem;
				margin-left: .18rem;
			}
			&_icon {
				display: inline-block;
				margin-right: .1rem;
				vertical-align: top;
				color: #0091FF;
				font-size: .2rem;
			}
			&_clear {
				flex: 1;
				margin-right: .16rem;
				text-align: right;
				&_btn {
					display: inline-block;
				}
			}
		}
		&_item {
			position: relative;
			display: flex;
			padding: .12rem 0;
			margin: 0 .16rem;
			border-bottom: .01rem solid #F1F1F1;
			&_checked {
				line-height: .5rem;
				margin-right: .2rem;
				color: #0091FF;
				font-size: .2rem;
			}
			&_img {
				width: .46rem;
				height: .46rem;
				margin-right: .16rem;
			}
			&_title {
				margin: 0;
				line-height: .2rem;
				font-size: .14rem;
				color: #333;
			}
			&_price {
				margin:.06rem 0 0 0;
				line-height: .2rem;
				font-size: .14rem;
				color: #E93B3B;
			}
			&_yen {
				font-size: .12rem;
			}
			&_origin {
				margin-left: .06rem;
				line-height: .2rem;
				font-size: .12rem;
				color: #999;
				text-decoration: line-through;
			}
		}
		.product_number {
			position: absolute;
			right: 0;
			bottom: .12rem;
			&_minus,&_plus {
				display: inline-block;
				width: .2rem;
				height: .2rem;
				line-height: .16rem;
				border-radius: 50%;
				font-size: .2rem;
				text-align: center;
			}
			&_minus {
				border: .01rem solid #666;
				color: #666;
				margin-right: .05rem;
			}
			&_plus {
				background: #0091FF;
				color: #FFF;
				margin-left: .05rem;
			}
		}
	}
.check {
	display: flex;
	height: .49rem;
	border-top: .01rem solid #F1F1F1;
	line-height: .49rem;
	&_icon {
		position: relative;
		width: .84rem;
		&_img {
			display: block;
			margin: .12rem auto;
			width: .28rem;
			height: .26rem;
		}
		&_tag {
			position: absolute;
			right: .2rem;
			top: .04rem;
			width: .2rem;
			height: .2rem;
			line-height: .2rem;
			background-color: #E93B3B;
			border-radius: 50%;
			font-size: .12rem;
			text-align: center;
			color: #fff;
			transform: scale(.5);
		}
	}
	&_info {
		flex: 1;
		color: #333;
		font-size: .12rem;
		&_price {
			line-height: .49rem;
			color: #E93B3B;
			font-size: .18rem;
		}
	}
	&_btn {
		width: .98rem;
		background-color: #4FB0F9;
		text-align: center;
		color: #FFF;
		font-size: .14rem;
		a {
			color: #FFF;
			text-decoration: none;
		}
	}
}
</style>
