<template>
	<div class="wrapper">
		<div class="search">
			<div class="search_back iconfont" @click="handleBackClick()">&#xe613;</div>
			<div class="search_content">
				<span
					class="search_content_icon iconfont"
				>&#xe603;</span>
				<input
					class="search_content_input"
					placeholder="请输入商品名称"
				/>
			</div>
		</div>
		<ShopInfo :item='item'/>
		<Content></Content>
		<Cart></Cart>
	</div>
</template>

<script>
	import ShopInfo from '../../components/Shopinfo.vue'
	import{ useRouter,useRoute } from 'vue-router';
	import Content from './Content.vue'
	import Cart from './Cart.vue'
	import { get } from '../../utils/request.js'
	import { ref } from 'vue'
	export default {
		name:'Shop',
		components:{ ShopInfo,Content,Cart },
		setup() {
			const router = useRouter();
			const route = useRoute();
			const item = ref([]);
			const getNearbyList2 = async () => {
				const result = await get('/api/shop/hot-list')
				if(result?.errno === 0) {
					item.value = result.data[route.params.id-1]
				}
			}
			getNearbyList2()
			const handleBackClick = ()=>{
				router.back()
			}
			return { item,handleBackClick }
		}
	}
</script>

<style lang="scss" scoped>
	.wrapper {
		padding: 0 .18rem
	}
	.search {
		display: flex;
		margin: .14rem 0 .04rem 0;
		line-height: .32rem;
		&_back {
			width: .3rem;
			font-size: .24rem;
			color: #B6B6B6;
		}
		&_content {
			display: flex;
			flex: 1;
			background: #F5F5F5;
			border-radius: .16rem;
			&_icon {
				width: .44rem;
				text-align: center;
				color: B7B7B7;
			}
			&_input {
				display: block;
				width: 100%;
				padding-right: .2rem;
				border: none;
				outline: none;
				background: none;
				height: .32rem;
				&::placeholder {
					color: #333;
				}
			}
			
		}
	}
</style>