<template>
		<div class="product">
			<div 
				class="product_item"
				v-for="item in contentList"
				:key="item._id"
			>
				<img class="product_item_img" :src="item.imgUrl"/>
				<div class="product_item_detail">
					<h4 class="product_item_title">{{item.name}}</h4>
					<p class="product_item_sales">月售：{{item.sales}}件</p>
					<p class="product_item_price">
						<span class="product_item_yen">&yen;{{item.price}}</span>
						<span class="product_item_origin">&yen;{{item.oldPrice}}</span>
					</p>
				</div>
				<div class="product_number">
					数量：{{list[item._id-1]}}
				</div>
				</div>
				
		</div>
	<Docker :lt='1'></Docker>
</template>

<script>
	import { useRouter,useRoute } from 'vue-router'
	import {reactive,toRefs} from 'vue';
	import {get} from '../../utils/request.js';
	import { useStore } from 'vuex'
	import Docker from '../home/Docker.vue'
	export default{
		name:'Carlist',
		components:{Docker},
		setup(){
			const list1 = localStorage.getItem('productList1')
			const list2 = localStorage.getItem('productList2')
			const list3 = localStorage.getItem('productList3')
			const list4 = localStorage.getItem('productList4')
			const list = [
				list1,
				list2,
				list3,
				list4,
			]
			const categories = [
				{name:'全部商品',tab:'all'},
				{name:'秒杀',tab:'seckill'},
				{name:'新鲜水果',tab:'fruit'},
				
			]
			
			const data = reactive({
				currentTab:categories[0].tab,
				contentList:[]
			});
			const route = useRoute()
			const shopId = route.params.id
			const store = useStore()
			const { cartList } = toRefs(store.state)
			const addItemToCart = (shopId,productId,productInfo,num) =>{
				store.commit('addItemToCart',{shopId,productId,productInfo,num})
			}
			const getContent = async (tab) => {
				const result = await get('/api/shop/1/products',{tab})
				if(result?.errno === 0){
					data.contentList = result.data
				}
			}
			const handleClick = (tab) => {
				getContent(tab)
				data.currentTab = tab
			}
			getContent('all')
			
			const { contentList,currentTab} = toRefs(data)
			return {contentList,currentTab,categories,handleClick,cartList,shopId,addItemToCart,list}
		}
		}
</script>

<style lang="scss" scoped>
	.product {
		overflow-y: scroll;
		flex: 1;
		background: #FFFFFF;
		&_header {
			display: flex;
			line-height: .52rem;
			border-bottom: 1px solid #F1F1F1;
			font-size: .14rem;
			color: #333;
			&_checked {
				line-height: .5rem;
				margin-right: .2rem;
				color: #0091FF;
				font-size: .2rem;
			}
			&_all {
				width: .78rem;
				margin-left: .18rem;
			}
			&_icon {
				display: inline-block;
				margin-right: .1rem;
				vertical-align: top;
				color: #0091FF;
				font-size: .2rem;
			}
			&_clear {
				flex: 1;
				margin-right: .16rem;
				text-align: right;
				&_btn {
					display: inline-block;
				}
			}
		}
		&_item {
			position: relative;
			display: flex;
			padding: .12rem 0;
			margin: 0 .16rem;
			border-bottom: .01rem solid #F1F1F1;
			&_checked {
				line-height: .5rem;
				margin-right: .2rem;
				color: #0091FF;
				font-size: .2rem;
			}
			&_img {
				width: .46rem;
				height: .46rem;
				margin-right: .16rem;
			}
			&_title {
				margin: 0;
				line-height: .2rem;
				font-size: .14rem;
				color: #333;
			}
			&_price {
				margin:.06rem 0 0 0;
				line-height: .2rem;
				font-size: .14rem;
				color: #E93B3B;
			}
			&_yen {
				font-size: .12rem;
			}
			&_origin {
				margin-left: .06rem;
				line-height: .2rem;
				font-size: .12rem;
				color: #999;
				text-decoration: line-through;
			}
		}
		.product_number {
			position: absolute;
			right: 0;
			bottom: .12rem;
			&_minus,&_plus {
				display: inline-block;
				width: .2rem;
				height: .2rem;
				line-height: .16rem;
				border-radius: 50%;
				font-size: .2rem;
				text-align: center;
			}
			&_minus {
				border: .01rem solid #666;
				color: #666;
				margin-right: .05rem;
			}
			&_plus {
				background: #0091FF;
				color: #FFF;
				margin-left: .05rem;
			}
		}
	}
</style>
