<template>
	<div class="wrapper">
		<img class="wrapper_img" src="../../../public/img/user.png"/>
		<div class="wrapper_input">
			<input class="wrapper_input_content" placeholder="请输入手机号" v-model="data.username"/>
		</div>
		<div class="wrapper_input">
			<input class="wrapper_input_content" placeholder="请输入密码" v-model="data.password1"/>
		</div>
		<div class="wrapper_input">
			<input class="wrapper_input_content" placeholder="确认密码" v-model="data.password2"/>
		</div>
		<div class="wrapper_login-button" @click="handleRegister()">注册</div>
		<div class="wrapper_login-link" @click="handleLogin()">已有账号去登录</div>
	</div>
	<Toast v-if="data.showToast" :message='data.toastMessage'/>
</template>

<script>
	import Toast from '../../components/Toast.vue'
	import { post } from '../../utils/request.js'
	import { useRouter } from 'vue-router';
	import axios from 'axios'
	import { reactive } from 'vue'
	export default {
		name:'Register',
		components:{Toast},
		setup(){
			const data = reactive({
				username:'',
				password1:'',
				password2:'',
				password:'',
			})
			const router = useRouter();
			const showToast = (mes) =>{
				data.showToast = true
				data.toastMessage = mes
				setTimeout(()=>{
						data.showToast =  false
						data.toastMessage = ''
				},2000)
			}
			const handleRegister = async() => {
				if(data.password1==data.password2){
					data.password=data.password1
					try{
						const result = await post('/api/user/login',{
							username:data.username,
							password:data.password
						})
					if(result?.errno === 0){
						localStorage.isLogin = true;
						router.push({name:'Home'})
					}else{
						showToast('注册失败')
						
					}
					}catch(e){
						showToast('请求失败')
					}
			}else{
				showToast('密码不一致，请重新输入')
			}
			}
			const handleLogin = () => {
				router.push({name:'Login'})
			}
			return { data,handleLogin,handleRegister }
			}
		}
</script>


<style lang="scss" scoped>
	.wrapper {
		position: absolute;
		top: 40%;
		left: 0;
		right: 0;
		overflow-y: visible;
		transform: translateY(-50%);
		&_img {
			display: block;
			margin: 0 auto .4rem auto;
			width: .66rem;
			height: .66rem;
		}
		&_input {
			height: .48rem;
			margin: 0 .4rem .16rem .4rem;
			padding: 0 .16rem;
			background: #F9F9F9;
			border: 1px solid rgba(0,0,0,0.1);
			border-radius: 6px;
			&_content {
				line-height: .48rem;
				border: none;
				outline: none;
				width: 100%;
				background: none;
				font-size: .16rem;
				color: #777;
				&::placeholder {
					color: #777;
				}
			}
		}
		&_login-button {
			margin: .32rem .4rem .16rem .4rem;
			line-height: .48rem;
			background: #0091FF;
			box-shadow: 0 .04rem .08rem 0 rgba(0,145,255,0.32);
			border-radius: .04rem;
			color: #fff;
			font-size: .16rem;
			text-align: center;
		}
		&_login-link {
			font-size: .14rem;
			text-align: center;
			color: #777;
		}
	}
</style>
